"use client";

import { useEffect, useState } from "react";
import { api } from "../api";
import { getUser } from "../auth";

export default function Restaurants() {
  const user = getUser();
  const [restaurants, setRestaurants] = useState([]);

  useEffect(() => {
    api.get(`/restaurants`).then(setRestaurants);
  }, []);

  return (
    <div>
      <h2>Restaurants</h2>

      {restaurants.length === 0 && <p className="empty">No restaurants found</p>}

      {restaurants.map(r => (
        <div key={r.id} className="restaurant-card">
           <img src={r.image || "/restaurants.jpeg"} alt={r.name} className="card-img" />
          <div>
            <b>{r.name}</b>
            <div style={{ fontSize: "0.9rem", color: "#6b7280" }}>
              {r.country}
            </div>
          </div>
          <button
            onClick={() => {
              localStorage.setItem("restaurantId", r.id);
              window.location.href = "/menu";
            }}
          >
            View Menu
          </button>
        </div>
      ))}
    </div>
  );
}
